.link-up {
  position: fixed;
  right: 150px;
  bottom: 20px;

  display: flex;
  align-items: center;
  justify-content: center;

  width: 45px;
  height: 45px;
  border-radius: 50%;
  border: 1px solid $bg-modal-active-btn;
  background-color: $color-white;
  z-index: 15;

  transform: translateY(100px);
  transition: transform 300ms ease-in-out;
}

